$slick-arrow-color: white !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: url(/svg/arrow-left.svg) !default;
$slick-next-character: url(/svg/arrow-right.svg) !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

.serviceslider {
  margin: 100px auto 170px auto;
  //max-width: 1200px
  &__info {
    position: absolute;
    top: 10%;
    right: 10%;
    width: 30%;
    &--left {
      &.serviceslider__info {
        left: 10%;
        right: unset;
        .serviceslider__btn {
          margin-top: 20px;
          margin-right: auto;
          margin-left: unset; } } } }
  &__txt {
    font-style: normal;
    font-weight: 800;
    font-size: 42px;
    line-height: 58px;
    color: $color-dark;
 }    //transform: translateY(-100%)
  &__btn {
    margin-top: 20px;
    margin-left: auto;
    max-width: 380px; }

