  .slick-next {
    right: -25px; }

  .slick-prev {
    left: -25px;
    [dir="rtl"] & {
      left: auto;
      right: -20px; }

    &:before {
      content: $slick-prev-character;
      [dir="rtl"] & {
        content: $slick-next-character; } } }

  .slick-dots {
    display: flex;
    list-style: none;
    justify-content: center;
    padding: 0;
    margin-top: 33px;
    li {
      button {
        font-size: 0;
        width: 15px;
        height: 15px;
        display: block;
        background: #fff;
        border-radius: 50%;
        border: 1px solid #000; }
      + li {
        margin-left: 10px; } }
    li.slick-active {
      button {
        background: #000; } } } }

