.form {
  &__group {
    position: relative;
    width: 100%;
    &:last-child {
      width: 100%;
      margin: 50px 0 100px 0!important; }
    &+.form__group {
      margin-left: unset;
      margin-top: 40px; } }
  &__fix {
    display: flex;
    flex-wrap: wrap;
    padding: 0 20px;
    max-width: 100%;
    margin: 0 auto;
    &--txt {
      border: 1px solid $color-white;
      box-sizing: border-box;
      border-radius: 40px;
      width: 100%;
      height: 141px;
      background: $color-blue;
      color: $color-white; }

    &--name,
    &--phone {
      border: 1px solid $color-white;
      box-sizing: border-box;
      border-radius: 40px;
      background: $color-blue;
      color: $color-white;
      height: 60px; }
    &--name,
    &--phone {
      width: 100%; }
    label {
      position: absolute;
      color: $color-white;
      background: $color-blue;
      top: -17px;
      left: 40px;
      padding: 5px; }
    .btn-blue {
      width: 100%;
      box-shadow: -2px 2px 4px rgba(2, 30, 62, 0.2), 2px -2px 4px rgba(2, 30, 62, 0.2), -2px -2px 4px rgba(10, 118, 248, 0.9), 2px 2px 5px rgba(2, 30, 62, 0.9);
      &:before {
        content: unset; } } } }

@media screen and (min-width: 768px) {
  .form {
    &__group {
 }      //width: 50%
    &__fix {
      padding: unset;
      max-width: 50%;
      margin: 0 auto;
      .btn-blue {
        width: 100%; } } } }

@media screen and (min-width: 1250px) {
  .form {
    &__group {
      width: unset;
      &+.form__group {
        margin-left: 40px;
        margin-top: unset; } }
    &__fix {
      padding: unset;
      max-width: unset;
      margin: unset;
      &--name {
        width: 240px; }
      &--phone {
        width: 360px; } } } }
