.box4x {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 100px 0;
  & + .box4x {
    margin-left: 50px; }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 1 100%; }
  &__txt {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    max-width: 225px;
    margin: 28px auto; }
  &__ico {
    width: 102px;
    margin: 0 auto; } }

@media screen and (min-width: 768px) {
  .box4x {
    & + .box4x {
      margin-left: 50px; }
    &__item {
      flex: 1 1 50%; } } }

@media screen and (min-width: 1250px) {
  .box4x {
    &__item {
      flex: 1 1 auto; } } }

@media screen and (min-width: 1680px) {
  .box4x {
    padding: 0 0 155px 0; } }
