*,
*:before,
*:after {
  box-sizing: inherit; }

* {
  outline: 0 !important; }

*::-moz-focus-outer,
*::-moz-focus-inner {
  border: 0;
  padding: 0; }

html {
  box-sizing: border-box;
  font-family: $font;
  font-weight: 400; }

body {
  font-family: $font;
  font-weight: 400;
  position: relative;
  scroll-behavior: smooth;
  font-size: 18px;
  padding: 0;
  margin: 0; }

.wrapper {
  &__content {
    @include clearfix();
    &--long {
      max-width: 1680px;
      margin: 0 auto; }
    &--short {
      max-width: 1320px;
      margin: 0 auto; } } }

h1, h2, h3, h4, h5, h6 {
  letter-spacing: 0;
  color: $color-black;
  font-weight: 400;
  font-family: $font; }

h1, h2 {
  font-size: 42px;
  margin: 80px 0;
  font-weight: 800;
  margin-left: 20px; }

h3 {
  font-weight: 800;
  font-size: 28px;
  line-height: 39px; }

a {
  text-decoration: none;
  color: $color-dark;
  transition: $trans-main;
  &:hover {
    color: $color-blue; } }

.is-red {
  color: $color-red; }

.is-red-bg {
  background: $color-red; }

.is-blue {
  color: $color-blue; }

.is-blue-bg {
  background: $color-blue; }

.is-grey {
  color: $color-grey; }

.is-grey {
  background: $color-grey; }

.is-dark {
  color: $color-dark; }

.is-dark-bg {
  background: $color-dark; }

.is-white {
  color: $color-white; }

.is-white-bg {
  background: $color-white; }

.padding100 {
  padding: 10px 0; }

.margin100 {
  margin: 100px 0; }

@media screen and (min-width: 768px) {
  .padding100 {
    padding: 100px 0; } }

@media screen and (min-width: 1200px) {
  .pad-left-300 {
    padding-left: 175px;
    padding-top: 200px; } }

@media screen and (min-width: 1360px) {
  .no-wrap {
    white-space: nowrap; }

  h1, h2 {
    margin-left: unset; } }
