.mainslider {
  &__box {
    position: relative; }
  &__img {
    img {
      margin: 0 0 0 auto; } }
  &__txt {
    font-style: normal;
    font-weight: 800;
    font-size: 42px;
    line-height: 58px;
    color: $color-dark; }
  &__btn {
    margin-top: 35px;
    max-width: 380px; }
  &__info {
    width: 38%;
    position: absolute;
    top: 54%;
    left: 10%;
    transform: translateY(-100%); }

  // Arrows */
  .slick-prev,
  .slick-next {
    position: absolute;
    display: block;
    height: 100px;
    width: 30px;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background: transparent;
    color: transparent;
    top: 50%;
    -webkit-transform: translate(0, -100%);
    -ms-transform: translate(0, -100%);
    transform: translate(0, -100%);
    padding: 0;
    border: none;
    outline: none;
    transition: $trans-main;
    &:hover { //, &:focus
      outline: none;
      background: transparent;
      color: transparent;
      filter: invert(45%) sepia(18%) saturate(5082%) hue-rotate(358deg) brightness(99%) contrast(110%);
      &:before {
        opacity: $slick-opacity-on-hover; } }


    &.slick-disabled:before {
      opacity: $slick-opacity-not-active; }

    &:before {
      height: 34px;
      width: 36px;
      display: block;
      font-size: 20px;
      content: $slick-next-character;
      line-height: 1;
      color: $slick-arrow-color;
      opacity: $slick-opacity-default;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale; } }

  .slick-next {
    right: 25px; }

  .slick-prev {
    left: 25px;
    z-index: 1;
    [dir="rtl"] & {
      left: auto;
      right: -20px; }

    &:before {
      content: $slick-prev-character;
      [dir="rtl"] & {
        content: $slick-next-character; } } } }
